import React, {
  useContext,
  useRef,
  useEffect,
  useState,
} from 'react';
import cx from 'classnames';
import useMedia from 'use-media';
import { Locked16 } from '@carbon/icons-react';
import NavContext from '../../util/context/NavContext';
import { nav, open, divider, link, linkDisabled } from './Switcher.module.scss';


const Switcher = ({ children }) => {
  const lgBreakpoint = useMedia('min-width: 1056px');
  const { switcherIsOpen, toggleNavState } = useContext(NavContext);
  const listRef = useRef(null);
  const [height, setHeight] = useState(0);

  useEffect(() => {
    const collapseOpenNavs = function (e) {
      if (e.which === 27) {
        toggleNavState('switcherIsOpen', 'close');
      }
    };

    document.addEventListener('keyup', collapseOpenNavs);

    return function cleanup() {
      document.removeEventListener('keyup', collapseOpenNavs);
    };
  }, [toggleNavState]);

  // calculate and update height
  useEffect(() => {
    if (switcherIsOpen) {
      setHeight(listRef.current.offsetHeight + 40);
    } else {
      setHeight(0);
    }
  }, [listRef, switcherIsOpen]);

  const maxHeight = !lgBreakpoint && switcherIsOpen ? '100%' : `${height}px`;

  return (
    <nav
      className={cx(nav, { [open]: switcherIsOpen })}
      aria-label="IBM Design ecosystem"
      tabIndex="-1"
      style={{ maxHeight }}>
      <ul ref={listRef}>{children}</ul>
    </nav>
  );
};

export const SwitcherDivider = (props) => (
  <li className={divider}>
    <span {...props} />
  </li>
);

export const SwitcherLink = ({
  disabled,
  children,
  isInternal,
  href: hrefProp,
  ...rest
}) => {
  const href = disabled || !hrefProp ? undefined : hrefProp;
  const className = disabled ? linkDisabled : link;
  const { switcherIsOpen } = useContext(NavContext);
  const openTabIndex = disabled ? '-1' : 0;

  return (
    <li>
      <a
        aria-disabled={disabled}
        role="button"
        tabIndex={switcherIsOpen ? openTabIndex : '-1'}
        className={className}
        href={href}
        {...rest}>
        {children}
        {isInternal && <Locked16 />}
      </a>
    </li>
  );
};

// https://css-tricks.com/using-css-transitions-auto-dimensions/
// Note: if you change this, update the max-height in the switcher stylesheet
const DefaultChildren = () => (
  <>
    <SwitcherDivider>Blogs</SwitcherDivider>
    <SwitcherLink href="/blog/getting-started-with-alvearie" isInternal>
      Getting Started with Project Alvearie
    </SwitcherLink>
    <SwitcherLink href="https://developer.ibm.com/components/kafka/blogs/4-reasons-to-join-project-alvearie-ibms-open-source-healthcare-effort/">
      4 reasons to join open source Project Alvearie and help solve healthcare’s toughest problems
    </SwitcherLink>
    <SwitcherLink href="/blog/clinical-records-ingestion-pattern" isInternal>
      Using Open Source to Build a Healthcare Record Ingestion Pattern
    </SwitcherLink>
    <SwitcherDivider>Contribute</SwitcherDivider>
    <SwitcherLink href="/contributions/requestSlackAccess">
      Request access to Alvearie Slack
    </SwitcherLink>
    <SwitcherLink href="http://alvearie.slack.com">
      Login to Alvearie Slack
    </SwitcherLink>
    <SwitcherDivider>Other Links</SwitcherDivider>
   <SwitcherLink href="https://www.ibm.com/watson-health/solutions/healthcare-interoperability">
      Healthcare interoperability solutions at IBM
    </SwitcherLink>
 
  </>
);

Switcher.defaultProps = {
  children: <DefaultChildren />,
};

export default Switcher;
